import React from "react";
import classes from "./FormEquipment.module.css";
import connection from "../../axios";
import {useState} from "react";


function FormEquipment() {
    const [name, setName] = useState('');
    const [price, setPrice] = useState('');
    const [confirmationMessage, setConfirmationMessage] = useState(''); // Nowy stan dla potwierdzenia

    const submitEquipment = () => {
        console.log(name + " " + price);

        let createEquipment = {
            'nameEquipment': name,
            'priceEquipment': price
        };

        connection.post("/api/equipments/add", createEquipment)
            .then((response) => {
                console.log("Odpowied serwera:", response);
                setConfirmationMessage("Sprzt zosta pomylnie dodany!"); // Ustawiamy komunikat sukcesu
                setName(''); // Resetujemy pola po udanym dodaniu
                setPrice('');
            })
            .catch((error) => {
                console.log("Bd:", error);
                setConfirmationMessage("Wystpi bd podczas dodawania sprztu, upewnij si czy masz uprawnienia");
            });
    };

    return (
        <div className={classes.FormEquipment}>
            <div className={classes.GridContainer}>
                <label htmlFor="input name">Nazwa Sprztu</label>
                <input
                    id={'input-name'}
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                />
                <label htmlFor="{'input-price'}">Cena sprztu</label>
                <input
                    id={'input-price'}
                    type="number"
                    value={price}
                    onChange={(e) => setPrice(e.target.value)}
                />
            </div>
            <button className={classes.Confirm} onClick={submitEquipment}>Zatwierd</button>
            {/* Wywietlanie komunikatu potwierdzenia */}
            {confirmationMessage && <p>{confirmationMessage}</p>}
        </div>
    );
}

export default FormEquipment;