import React, { useState } from "react";
import connection from "../../axios";

const DeleteClient = () => {
    const [idClient, setIdClient] = useState('');
    const [message, setMessage] = useState('');

    const handleDelete = () => {
        connection.delete(`/api/clients/delete`, { params: { idClient } })
            .then((response) => {
                setMessage(`Klient o ID ${idClient} zosta usunity.`);
                console.log("Klient usunity:", response.data);
            })
            .catch((error) => {
                console.error("Bd usuwania klienta:", error);
                setMessage('Wystpi bd przy usuwaniu klienta.');
            });
    };

    return (
        <div>
            <h2>Usu klienta</h2>
            <div>
                <label>Podaj ID</label>
            <input
                type="number"
                value={idClient}
                onChange={(e) => setIdClient(e.target.value)}
                placeholder="Wprowad ID klienta"
            />
        </div>
            <button onClick={handleDelete}>Usu klienta</button>

            {message && <p>{message}</p>}
        </div>
    );
};

export default DeleteClient;
