import React, { useState } from 'react';
import { Button, Form, Alert } from 'react-bootstrap';
import connection from "../../axios";
import { useNavigate } from 'react-router-dom';

const EquipmentDelete = () => {
    const [idEquipment, setIdEquipment] = useState('');
    const [successMessage, setSuccessMessage] = useState('');
    const [errorMessage, setErrorMessage] = useState('');
    const navigate = useNavigate();

    const handleSubmit = (event) => {
        event.preventDefault();

        // Wykonanie dania DELETE do backendu
        connection.delete("/api/equipments/delete", {params:{idEquipment}})
            .then((response) => {
                console.log("Sprzt usunity:", response);
                setSuccessMessage(`Sprzt o ID ${idEquipment} zosta pomylnie usunity.`);
                setErrorMessage('');
                setIdEquipment(''); // Wyczy pole po pomylnym usuniciu
                setTimeout(()=> {
                    navigate("/list");
                },2000);
            })
            .catch((error) => {
                console.error("Bd podczas usuwania sprztu:", error);
                setErrorMessage("Nie udao si usun sprztu. Upewnij si, e sprzt istnieje, lub czy masz uprawnienia");
                setSuccessMessage('');
            });
    };

    return (
        <div>
            <h2>Usu Sprzt</h2>
            <Form onSubmit={handleSubmit}>
                <Form.Group controlId="formEquipmentId">
                    <Form.Label>Podaj ID sprztu do usunicia</Form.Label>
                    <Form.Control
                        type="number"
                        value={idEquipment}
                        onChange={(e) => setIdEquipment(e.target.value)}
                        placeholder="ID sprztu"
                        required
                    />
                </Form.Group>
                <Button variant="danger" type="submit">Usu Sprzt</Button>
            </Form>
            {successMessage && <Alert variant="success" className="mt-3">{successMessage}</Alert>}
            {errorMessage && <Alert variant="danger" className="mt-3">{errorMessage}</Alert>}
        </div>
    );
};

export default EquipmentDelete;
