import React, { useState } from 'react';
import {Link, useNavigate} from 'react-router-dom';
import connection from "../../axios";
import {Button, Col} from "react-bootstrap";
import classes from "./Login.module.css";

const Login = () => {
    const [appUserName, setAppUserName] = useState('');
    const [password, setPassword] = useState('');
    const [nameUser, setNameUser] = useState('');  // Nowe pole
    const navigate = useNavigate();
    const [error, setError] = useState('')
    const [successMessage, setSuccesMessage] = useState('');


    const handleSubmit = (event) => {
        event.preventDefault();

        // Przechowywanie danych logowania w localStorage
        localStorage.setItem('username', appUserName);
        localStorage.setItem('password', password);
        localStorage.setItem('nameUser', nameUser);

        connection.post("/api/appusers/login", {appUserName, password})
            .then(response => {
                console.log("Logowanie udane:", response);
                setSuccesMessage("Zalogowano poprawnie")
                getCompanyData(appUserName);
                window.location.reload(); // Refresh the page after 5 seconds
                setTimeout(()=>{
                    navigate("/");
                },3000);

            })
            .catch(err => {
                console.error("Bd logowania:", err);
                if (err.response && err.response.status === 401) {
                    setError("Bdna nazwa uytkownika lub haso.");
                } else {
                    setError("Wystpi bd. Sprbuj ponownie.");
                }
            });
    };

    const getCompanyData = (loginUser) => {
        // connection.get(`/api/company/data`, { params: { loginUser } })
        connection.get(`/api/company/data/${loginUser}`)
            .then(response => {
                console.log("Dane firmy pobrane:", response.data);

                // Przechowywanie danych firmy w localStorage lub innym miejscu
                localStorage.setItem('companyData', JSON.stringify(response.data));

            })
            .catch(err => {
                console.error("Bd pobierania danych firmy:", err);
            });
    };
    return (
        <>
            <form onSubmit={handleSubmit} className={classes.LoginForm}>
                <input
                    type="text"
                    value={appUserName}
                    onChange={(e) => setAppUserName(e.target.value)}
                    placeholder="Nazwa uytkownika"
                    required
                    className={classes.InputField}
                />
                <input
                    type="password"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    placeholder="Haso"
                    required
                    className={classes.InputField}
                />
                <input
                    type="text"
                    value={nameUser}
                    onChange={(e) => setNameUser(e.target.value)}  // Obsuga pola nameUser
                    placeholder="Wprowad nazw uytkownika"
                    required
                    className={classes.InputField}
                />
                <button type="submit" className={classes.SubmitButton}>Zaloguj si</button>
                {error && <p className={classes.ErrorText}>{error}</p>}
                <Col>
                    <Button variant="primary" onClick={() => navigate('/companySave')} className={classes.ActionButton}>
                        Wprowad dane firmy
                    </Button>
                </Col>
                <Col>
                    <Button variant="outline-secondary" onClick={() => navigate('/logout')}
                            className={classes.ActionButton}>
                        <div>Wylogowanie</div>
                    </Button>
                </Col>
            </form>
            <div className={classes.Footer}>
                Program napisaa firma Mandragora. Kontakt w celu zakupu: tel.502109609
            </div>
        </>
    );
}
export default Login;