import React, { useState } from 'react';
import connection from "../../axios"; // Importowanie instancji Axios

const DeleteUser = () => {
    const [idUser, setIdUser] = useState('');  // Przechowuje ID uytkownika do usunicia
    const [error, setError] = useState('');  // Przechowuje informacje o bdach
    const [successMessage, setSuccessMessage] = useState('');  // Przechowuje komunikaty o sukcesie

    const handleDelete = async (event) => {
        event.preventDefault();  // Zapobiega domylnemu odwieeniu strony

        try {
            // Wysyanie dania DELETE na endpoint
            await connection.delete(`/api/appusers/devel/delete/${idUser}`);

            // Ustawienie komunikatu o sukcesie, jeli uytkownik zostanie pomylnie usunity
            setSuccessMessage(`User with ID ${idUser} was successfully deleted.`);
            setError('');  // Wyczy ewentualne bdy
            setIdUser('');  // Wyczy pole ID po udanym usuniciu
        } catch (err) {
            console.error("Bd podczas usuwania uytkownika:", err);
            // Ustawienie komunikatu o bdzie, jeli co poszo nie tak
            if (err.response) {
                setError(err.response.data.message || 'Wystpi bd podczas usuwania uytkownika.');
            } else {
                setError('Wystpi bd. Sprbuj ponownie.');
            }
        }
    };

    return (
        <div>
            <h2>Usu Uytkownika</h2>
            <form onSubmit={handleDelete}>
                <div>
                    <label>Podaj ID Uytkownika:</label>
                    <input
                        type="number"
                        value={idUser}
                        onChange={(e) => setIdUser(e.target.value)}  // Aktualizacja wartoci idUser
                        required
                    />
                </div>
                <button type="submit">Usu Uytkownika</button>
            </form>
            {successMessage && <p style={{ color: 'green' }}>{successMessage}</p>}  {/* Komunikat o sukcesie */}
            {error && <p style={{ color: 'red' }}>{error}</p>}  {/* Komunikat o bdzie */}
        </div>
    );
};

export default DeleteUser;
