import React, { useState } from 'react';
import connection from "../../axios";
import {useNavigate} from "react-router-dom";
import {Col} from "react-bootstrap";

const CreateUser = () => {
    const [appUserName, setAppUserName] = useState('');
    const [password, setPassword] = useState('');
    const [role, setRole] = useState(''); // Moe lepiej zrobi z tego pole select
    const [error, setError] = useState('');
    const [successMessage, setSuccessMessage ]= useState('');


       let navigate = useNavigate();
        const handleDeleteClick = ()=>{
            navigate('/deleteUser');
    }
        const handleListUserClick = ()=>{
            navigate('/userList')
        }

    const handleSubmit = async (event) => {
        event.preventDefault();

        const appUser = {
            appUserName,
            password,
            role,
        };
        try {
            const response = await connection.post('/api/appusers/devel/register', appUser);
            setSuccessMessage('Zarejestrowano uytkownika pomylnie');
            setError(''); // Wyczy ewentualne bdy


        } catch (err) {
            console.error("Bd rejestracji:", err);

            // Sprawdzenie, czy odpowied bdu istnieje i posiada dane
            if (err.response && err.response.data) {
                // Wycignij komunikat o bdzie
                setError(err.response.data.message || 'Wystpi bd podczas rejestracji.');
            } else {
                setError('Wystpi bd. Sprbuj ponownie.');
            }
        }
    }

        return (
            <div>
                <h2>Rejestracja Uytkownika - opcja dostpna tylko dla administratora</h2>
                <form onSubmit={handleSubmit}>
                    <div>
                        <label>Nadaj Nazw uytkownika:</label>
                        <input
                            type="text"
                            value={appUserName}
                            onChange={(e) => setAppUserName(e.target.value)}
                            required
                        />
                    </div>
                    <div>
                        <label>Nadaj Haso:</label>
                        <input
                            type="password"
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                            required
                        />
                    </div>
                    <div>
                        <label>Nadaj Rol Uytkownika:</label>
                        <input
                            value={role}
                            onChange={(e) => setRole(e.target.value)}
                            required
                        />
                    </div>
                    <button type="submit">Za konto</button>
                    <Col>
                        <button type="submit" onClick={handleDeleteClick}>
                            Usu konto
                        </button>
                    </Col>
                    <Col>
                        <button type="submit" onClick={handleListUserClick}>
                         Poka uytkownikw
                        </button>
                    </Col>
                </form>
                {successMessage && <p style={{color: 'green'}}>{"Zaoono konto poprawnie"}</p>}
                {error && <p style={{color: 'red'}}>{"Nie masz uprawnie"}</p>}
            </div>
        );
    };

export default CreateUser;
