import React from "react";
import {Card, Col, Row} from "react-bootstrap";
import styles from "./Home.module.css";
import classes from "../login/Login.module.css";
function Home() {
    return(
        <>        <div className={styles.ImageContainer}>
            <img src="https://picsum.photos/id/1036/1200/800" alt="Main" />
        </div>
        <Row className={styles.Row}>
            <Col className={styles.Col}>
                <Card className={styles.Card}>

                    <Card.Header className={styles.CardHeader}>Instrukcja - Smart Ski Rent</Card.Header>
                    <Card.Body>
                        <Card.Title className={styles.CardTitle}>
                            Login
                        </Card.Title>
                        <Card.Text className={styles.CardText}>
                            Logujesz si przy pomocy nazwy i hasa. Zwr uwag na wielko liter.
                            Login administratora pozwala dodawa nowy sprzt do bazy i go usuwa. Login Usera nie ma tych uprawnie
                        </Card.Text>
                    </Card.Body>
                    <Card.Footer className={styles.CardFooter}> Wane: Wprowa dane firmy,
                        ktre bd przypisane do Twojego loginu i bd drukowa si na umowie.
                        Jak masz dwa loginy lub wiecej to dla kadego musisz wprowadz dane firmy </Card.Footer>
                </Card>
            </Col>
            <Col className={styles.Col}>
                <Card className={styles.Card}>
                    <Card.Header className={styles.CardHeader}>Instrukcja - Smart Ski Rent </Card.Header>
                    <Card.Body>
                        <Card.Title className={styles.CardTitle}>
                            Klient
                        </Card.Title>
                        <Card.Text className={styles.CardText}>
                           Nowo dodany klient bdzie Ci si wywietla na pocztku listy.
                        </Card.Text>
                    </Card.Body>
                    <Card.Footer className={styles.CardFooter}> Wane: Pamitaj o zapisaniu numeru telefonu.
                        Bdzie on potrzebny gdyby chcia skorzysta z wyszukiwarki klientw,
                        poniewa wyszukujemy po nr telefonu.</Card.Footer>
                </Card>
            </Col>
            <Col className={styles.Col}>
                <Card className={styles.Card}>
                    <Card.Header className={styles.CardHeader}>Instrukcja - Smart Ski Rent  </Card.Header>
                    <Card.Body>
                        <Card.Title className={styles.CardTitle}>
                            Wypoycz
                        </Card.Title>
                        <Card.Text className={styles.CardText}>
                           Wybierz klienta z rozwijanej listy. Nowo dodany klient bdzie na samej grze.
                            Zaznacz sprzt ktry chcesz wypoyczy i kliknij utwrz wypoyczenie.
                            Program po 3 sekundach przeniesie Ci do listy wypoycze.

                        </Card.Text>
                    </Card.Body>
                    <Card.Footer className={styles.CardFooter}> Wskazwka: Moesz jednoczenie zaznaczy dowoln ilo sprztu ktry chcesz wypoyczy </Card.Footer>
                </Card>
            </Col>
            <div className={styles.ImageContainer}>
                <img src="https://picsum.photos/id/177/1200/800/" alt="Main"/>
            </div>
            <Col className={styles.Col}>
                <Card className={styles.Card}>
                    <Card.Header className={styles.CardHeader}>Instrukcja - Smart Ski Rent  </Card.Header>
                    <Card.Body>
                        <Card.Title className={styles.CardTitle}>
                          Lista wypoycze
                        </Card.Title>
                        <Card.Text className={styles.CardText}>
                           Zaznacz wypoyczenie na podstawie ktrego wydrukujesz umow z klientem.
                            Najnowsza bdzie na samej grze. Po zaznaczeniu wcinij Wyrukuj Umow. Kasujesz za wypoyczenie przy zwrocie.
                            Tutaj te zrobisz zwrot wypoyczenia. Zaznacz wypoyczenie do zwrotu.
                            Nacinij Zatwierd Zwroty. Znajd ponownie na licie zakoczone wypoyczenie i tam bd dostpne informacje:
                            Data Zwrot, Kwota Do Zapaty, Ilo Dni Wypoyczenia.
                        </Card.Text>
                    </Card.Body>
                    <Card.Footer className={styles.CardFooter}>Wskazwka: moesz zrobi zwrot wielu zaznaczonych wypoycze jednoczenie.
                        Wydruk umowy tylko po jednej na raz</Card.Footer>
                </Card>
            </Col>
            <Col className={styles.Col}>
                <Card className={styles.Card}>
                    <Card.Header className={styles.CardHeader}>Instrukcja - Smart Ski Rent </Card.Header>
                    <Card.Body>
                        <Card.Title className={styles.CardTitle}>
                          Sprzt
                        </Card.Title>
                        <Card.Text className={styles.CardText}>
                           Dodawa i usuwa sprz moe tylko uytkownik z uprawnieniami ADMIN.
                            Logika wypoyczenie polega na wielokrotnym wypoyczeniu tego samego zestawu.
                            Tak powinimy tworzy nazwy zestawu aby byo wiadomo o co chodzi, np. Buty snowbordowe lub Narty dorosy.
                            Cena moe zawiera grosze, np. 45,50.
                        </Card.Text>
                    </Card.Body>
                    <Card.Footer className={styles.CardFooter}>Wskazwka: Stwrz Narty dorosy1, Narty dorosy2, itd.
                        Myl e do 4, aby do jednego klienta jednorazowo, tworzc umow, przypisa 4 komplety</Card.Footer>
                </Card>
            </Col>
            <Col className={styles.Col}>
                <Card className={styles.Card}>
                    <Card.Header className={styles.CardHeader}>Instrukcja - Smart Ski Rent</Card.Header>
                    <Card.Body>
                        <Card.Title className={styles.CardTitle}>
                            Raporty
                        </Card.Title>
                        <Card.Text className={styles.CardText}>
                           Dostpny jest raport dzienny przychodw. Pamitaj, e klient paci, gdy zdaje sprzt.
                            Wprowad dat, dla ktrej system ma wygenerowa raport.
                        </Card.Text>
                    </Card.Body>
                    <Card.Footer className={styles.CardFooter}>Informacja: System nalicza pierwsz dob po stworzeniu wypoyczenia, kolejn dob po 24 godzinach od wypoyczenia.
                        Na przykad za 25 godzin wypoyczenia zapacisz za dwie doby.
                        </Card.Footer>
                </Card>
            </Col>
        </Row>
            <div className={classes.Footer}>
                Program napisaa firma Mandragora. Kontakt w celu zakupu: tel.502109609
            </div>
        </>

    )
}
export default Home;