import classes from "./EquipmentList.module.css";
import React from "react";
import {Grid} from "@mui/material";
import connection from "../../axios.js";
import {useEffect, useState} from "react";
import {useNavigate} from "react-router-dom";
import Button from "@mui/material/Button";

const EquipmentList = () => {
    const [nazwaZmiennej, setterDoKolekcji] = useState([]);
    const navigate = useNavigate(); // Hook do nawigacji

    useEffect(() => {
        const token = localStorage.getItem('token');
        console.log("Token:", token); // Dodaj to logowanie

        connection.get("/api/equipments")
            .then((response) => {
                if (Array.isArray(response.data)) {
                    setterDoKolekcji(response.data); // Ustaw dane, jeli to tablica
                } else {
                    setterDoKolekcji(response.data.equipments); // Jeli dane s w kluczu 'equipments'
                }
            })
            .catch((error) => {
                console.log("Bd podczas pobierania danych:", error);
            });
    }, []);
    // Przekierowanie do komponentu FormEquipment
    const goToAddEquipment = () => {
        navigate("/formEquipment"); // Przekierowanie do formularza dodawania sprztu
    };

    const goToDeleteEquipment = () => {
        navigate("/equipmentDelete"); // Przekierowanie do formularza dodawania sprztu
    };

    return (
        <div className={classes.Equipment}>
            <h2>Lista sprztu - opcje dostne tylko dla szefa </h2>

            <Button
                variant="contained"
                color="primary"
                onClick={goToAddEquipment}
                style={{ margin: '10px' }}>
                Dodaj Sprzt
            </Button>

            <div className={classes.EquipmentTableHeader}>
                <div className="one">Id</div>
                <div className="two">Nazwa sprztu</div>
                <div className="three">Cena</div>

            </div>
            {nazwaZmiennej.map((value) => (
                <Grid container className={classes.EquipmentTableRow} key={value.idEquipment}>
                    <Grid item xs={2}>{value.idEquipment}</Grid>
                    <Grid item xs={2}>{value.nameEquipment}</Grid>
                    <Grid item xs={2}>{value.priceEquipment}</Grid>

                </Grid>
            ))}
            <Button
                variant="contained"
                color="primary"
                onClick={goToDeleteEquipment}
                style={{ margin: '10px' }}>
                Usu Sprzt
            </Button>
        </div>
    );
}

export default EquipmentList;
