import React from "react";
import { Grid } from "@mui/material";
import moment from "moment";
import classes from "./DisplayRentalAgreement.module.css";
import {Button} from "react-bootstrap";
import {useState, useEffect} from "react";

function DisplayRentalAgreement({ renting }) {
    console.log("Dane wypoyczenia:", renting);

    const [companyData, setCompanyData] = useState({});

    useEffect(() => {
        // Pobieranie danych firmy z localStorage, jeli nie s jeszcze dostpne
        const storedCompanyData = JSON.parse(localStorage.getItem('companyData'));
        if (storedCompanyData) {
            setCompanyData(storedCompanyData);
        }
    }, []);



    // Format the renting date
    const dateRentingFormat = moment(renting.dateRenting).format('DD/MM/YY HH:mm');

    return (
        <Grid container className={classes.TableRow}>
            <Grid item xs={12} justifyContent={"center"}>
                <h4>Umowa wypoyczenia o nr: {renting.idRenting}</h4>
            </Grid>
            <Grid item xs={12}>Zostaa zawarta z dat: {dateRentingFormat} godzina.</Grid>
            <Grid item xs={12}>Na wypoyczenie sprztu zimowego</Grid>
            <Grid item xs={12}>Od firmy: {companyData.companyName|| "N/A"}</Grid>
            <Grid item xs={12}>ktra ma NIP: {companyData.companyNIP || "N/A"} </Grid>
            <Grid item xs={12}>Klient Pani/Pan: {renting.firstName} {renting.lastName}</Grid>
            <Grid item xs={12}>Nr telefonu kontaktowego: {renting.phoneNumber}</Grid>
            <Grid item xs={12}>Nr dowodu: {renting.identityCard}</Grid>
            <Grid item xs={12}>Wypoycza komplet zimowy o nazwie : {renting.nameEquipment}</Grid>
            <Grid item xs={12}>W cenie za dob: {renting.priceEquipment} z</Grid>
            <Grid item xs={12}>
                Patno za usug nastpi przy zwrocie sprztu, w cyklu 24 godzinnym za dob. Po miniciu 24 godzin obowizuj patnoc za kolejn dob.
                Klient zobowizuj si zwrci sprzt w stanie niepogorszonym. W przypadku uszkodzenia, kradziey zobowizuje si do pokrycia kosztw odtworzenia.
            </Grid>
            <Grid item xs={12} justifyContent={"flex-end"}>CZYTELNY PODPIS KLIENTA:</Grid>
            {/* Przycisk drukowania */}
            <div className={classes.ButtonPrint}>
                <Button variant="dark" onClick={() => window.print()}>
                    Drukuj umow
                </Button>
            </div>
        </Grid>

    );
}

export default DisplayRentalAgreement;
